.button {
    min-width: 100px;
    font-family: inherit;
    appearance: none;
    border: 1px solid #8e0039;
    border-radius: 5px;
    background: #ffc173;
    color: #24587a;
    padding: 8px 16px;
    font-size: 1rem;
    cursor: pointer;
  }
  
  button:hover {
    color: #0a67a3;
    background: #ff8e00;
  }